form {
    width: 90%;
    max-width: 300px;
    padding: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border-radius: 20px;
    border: solid 2px #f71e1e;
    background-color: #1d1c1c;
  }
  
  label {
    text-align: center;
    font-size: 25px;
    margin-bottom: 20px;
  }
  
  textarea {
    border: 2px solid #202329;
    border-radius: 2px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
    font-size: 20px;
    text-align: center;
    padding: 12px;
    height: 96px;
    width: calc(100% - 25px);
  }
  
  
  textarea:focus {
    outline-color: black; /*Change*/
  }
  
  .ButtonContainer {
    margin-top: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
  
  button {
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    width: 135px;
    height: 48px;
    border-radius: 10px;
    border: none;
  }


  
  .CancelButton:hover {
    background-color: #f05454;
    color: azure;
  }
  
  .SubmitButton:hover {
    background-color: #35bb4b;
    color: azure;
  }